.blogc {
  max-width: 1024px;
  color: #9e9c9c;
  margin: auto;
  margin-top: 50px;
}
.blog-img {
  position: relative;
  &::before {
    content: "";
    display: block;
    padding-top: 30%;
  }
  .ratio-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;

    img {
      width: 100%;
      max-height: 100%;
    }
  }
}
